import React, { useState, useEffect } from 'react';
import { Layout, ConfigProvider } from 'antd';
import './App.css';
import QuestionCard from './QuestionCard';
import Result from './Result';

const { Header, Content } = Layout;

interface Question {
  question: string;
  options: string[];
  correctIndex: number;
}

const App: React.FC = () => {
  const [questions, setQuestions] = useState<Question[]>([
    {
      question: "Ile lat temu zosta zaoony A-Team?",
      options: ["6", "8", "12", "15"],
      correctIndex: 2, // poprawna odpowied to A
    },
    {
      question: "Na jaki maksymalny okres zatrudniani s stayci? (miesice)",
      options: ["12", "18", "6", "24"],
      correctIndex: 1, // poprawna odpowied to A
    },
    {
      question: "Co oznacza skrt FLP?",
      options: ["Former Lame People", "Fantastic Leaders Poland", "First Leaders Performance", "Future Leaders Programme"],
      correctIndex: 3, // poprawna odpowied to A
    },
    {
      question: "Ile lat trwa FLP?",
      options: ["2 lata", "1,5 roku", "3 lata", "rok"],
      correctIndex: 0, // poprawna odpowied to A
    },
    {
      question: "Jaki procent osb po FLP zostaje w GSK po jego zakoczeniu?",
      options: ["76%", "33%", "76%", "100%"],
      correctIndex: 3, // poprawna odpowied to A
    },
    {
      question: "Ile spotka A-team odbyo si do tej pory?",
      options: ["10", "600", "150", "320"],
      correctIndex: 1, // poprawna odpowied to A
    },
  ]);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [quizFinished, setQuizFinished] = useState(false);

  // useEffect(() => {
  //   // Fetch questions from the JSON file
  //   fetch('/questions.json')
  //     .then((response) => response.json())
  //     .then((data) => setQuestions(data));
  // }, []);

  const handleAnswer = (isCorrect: boolean) => {
    if (isCorrect) {
      setScore(score + 1);
    }
  };

  const handleNext = () => {
    const nextIndex = currentQuestionIndex + 1;
    if (nextIndex < questions.length) {
      setCurrentQuestionIndex(nextIndex);
    } else {
      setQuizFinished(true);
    }
  };

  return (
    <ConfigProvider
      theme={{
        token: {
          colorPrimary: '#fa8c16', // Orange color
        },
      }}
    >
      <Layout style={{ minHeight: '100vh' }}>
        {/* Header */}
        <Header
          style={{
            backgroundColor: '#fff',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',

          }}
        >
          {/* <img src="/Logo.png" alt="Logo" style={{ height: '70px' }} /> */}
          <img
            src="https://upload.wikimedia.org/wikipedia/en/thumb/3/32/GSK_logo_2022.svg/1200px-GSK_logo_2022.svg.png"
            alt="Logo"
            style={{ height: '30px' }}
          />
        </Header>

        {/* Content */}
        <Content
          style={{
            padding: '20px',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          {questions.length > 0 ? (
            !quizFinished ? (
              <QuestionCard
                question={questions[currentQuestionIndex]}
                questionNumber={currentQuestionIndex + 1}
                totalQuestions={questions.length}
                onAnswer={handleAnswer}
                onNext={handleNext}
              />
            ) : (
              <Result score={score} totalQuestions={questions.length} />
            )
          ) : (
            <p>Loading questions...</p>
          )}
        </Content>
      </Layout>
    </ConfigProvider>
  );
};

export default App;
